   */
  event?: IModelEvent | null;
  /**
   * Data with match score stats.
   */
  scoreStats?: IEventScoreStats | null;
  /**
   * Data with match overview stats.
   */
  gameStats?: IEventGameStats | null;
}
const props = defineProps<EventStatsProps>();
</script>

