  </template>
  <template v-else-if="props.scoreStats || props.gameStats">
    <div class="max-lg:max-h-[49vh]">
      <div v-if="props.scoreStats">
        <EventStatsScoreboard
          :team1-name="props.event?.team1Name"
          :team2-name="props.event?.team2Name"
          :team1-logo-url="props.event?.team1LogoUrl"
          :team2-logo-url="props.event?.team2LogoUrl"
          :team1-color="props.event?.team1Color"
