          :team2-name="props.event?.team2Name"
          :team1-logo-url="props.event?.team1LogoUrl"
          :team2-logo-url="props.event?.team2LogoUrl"
          :team1-color="props.event?.team1Color"
          :team2-color="props.event?.team2Color"
          :stats-data="props.gameStats"
        />
      </div>
    </div>
