<script setup lang="ts">
import { UModalConfirm } from "#components";
import useTagFilters from "~/composables/tags/use-tag-filters";
import { useModal } from "~/composables/use-modal";
import TagsAPI from "~/services/tags";
import useEventStore from "~/stores/event";
import useUserStore from "~/stores/user";
import getErrorLocalePathMessage from "~/utils/locale";

const { t, te } = useI18n();
const modal = useModal();
const route = useRoute();
const toaster = useToast();
const userStore = useUserStore();
const eventStore = useEventStore();
const appConfig = useRuntimeConfig().public;

const searchValue = ref("");
const checkedTagsIds = ref([] as string[]);
const checkedAllTags = ref(false);

const isLoggedIn = computed(() => Boolean(userStore.user));
const isTaggingModeEnabled = computed(() => appConfig.APP_CUSTOMER.web_sdk.enable_tagging_mode);
const tags = computed(() => [...eventStore.tags.data.values()]);
const error = computed(() => eventStore.tags.error);
const hasPlayerError = computed(() => eventStore.playerState?.error !== null);
const isEventStartingSoon = computed(() => eventStore.isEventStartingSoon);
const playingTime = computed(() => eventStore.playerState?.currentTime || 0);
const isPlaying = computed(() => eventStore.playerState?.playing);
const userCanCreateGlobalTags = computed(() => Boolean(userStore.user?.canCreateGlobalTag));

const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};

const createTag = () => {
  eventStore.tags.isCreatingTag = true;
  eventStore.clips.isCreatingClip = false;
};

const deleteTags = (tagIds: string[]) => {
  modal.open(UModalConfirm, {
    heading: t("labels.modal_labels.delete_tag", 1),
    icon: "tag",
    confirmLabel: t("button.delete"),
    variant: "delete",
    confirmFn: () => {
      return TagsAPI.bulkDelete(tagIds)
        .then(() => toaster.success(t("pages.events.tabs.event_tags.tag_deleting_successfully_text")))
        .catch(onError);
    },
  });
};

const deleteTag = (tagId: string) => {
  modal.open(UModalConfirm, {
    heading: t("labels.modal_labels.delete_tag", 0),
    icon: "tag",
    confirmLabel: t("button.delete"),
    variant: "delete",
    confirmFn: () => {
      return TagsAPI.delete(tagId)
        .then(() => toaster.success(t("pages.events.tabs.event_tags.tag_deleting_successfully_text")))
        .catch(onError);
    },
  });
};

const editTag = (tagId: string) => {
  if (eventStore.tags.data.has(tagId))
    eventStore.tags.activeEditTag = eventStore.tags.data.get(tagId) || null;
  eventStore.tags.isCreatingTag = true;
};

const setActive = (state: boolean) => eventStore.player?.userActive(state);
const pauseVideo = () => eventStore.player?.pause();
const playVideo = (tagTime: number) => {
  eventStore.player?.currentTime(tagTime);
  return eventStore.player?.play();
};

const { data: filteredTags, filters, activeFilters } = useTagFilters(tags, { searchValue });
const isTagChecked = (tag: IModelTag) => checkedTagsIds.value.includes(tag.id);
const checkTag = (tag: IModelTag) => {
  const index = checkedTagsIds.value.indexOf(tag.id);

  if (index !== -1)
    checkedTagsIds.value.splice(index, 1);
  else
    checkedTagsIds.value.push(tag.id);
};

const checkAllTags = () => {
  filteredTags.value.forEach((tag) => {
    const index = checkedTagsIds.value.indexOf(tag.id);

    if (!checkedAllTags.value && index !== -1)
      checkedTagsIds.value.splice(index, 1);
    else if (checkedAllTags.value && !checkedTagsIds.value.includes(tag.id))
      checkedTagsIds.value.push(tag.id);
  });
};

const updateTagsValue = () => {
  checkedTagsIds.value = [];
  checkedAllTags.value = false;
};

watch(checkedAllTags, checkAllTags);
watch(eventStore.tags.data, updateTagsValue);
</script>

<template>
  <template v-if="error">
    <BaseNotFound :text="error" />
  </template>
  <template v-else-if="tags.length">
    <div class="h-full max-lg:max-h-[49vh] flex flex-col">
      <EventTagsHead
        v-model:search-value="searchValue"
        v-model:active-filters="activeFilters"
        v-model:is-all-tags-selected="checkedAllTags"
        :filters="filters"
        :checked-tags-total="checkedTagsIds.length"
      />
      <div v-if="!filteredTags.length" class="py-8 text-center text-secondary">
        {{ t("pages.events.tabs.event_tags.filters.no_tags") }}
      </div>
      <ul v-auto-animate class="max-lg:max-h-[calc(49vh-60px)] h-[calc(100%-60px)] overflow-auto pb-24">
        <li v-for="tag in filteredTags" :key="tag.id" class="py-1">
          <EventTagsListItem
            :id="tag.id"
            :title="tag.title"
            :is-playing="isPlaying"
            :playing-time="playingTime"
            :type="tag.type"
            :team-name="tag.tagTeam?.title"
            :has-menu="true"
            :has-menu-disabled="tag.global && !userCanCreateGlobalTags"
            :player-number="tag.playerNumber"
            :team-color="tag.tagTeam?.color"
            :start-time="tag.startTime"
            :end-time="tag.endTime"
            @stop="pauseVideo()"
            @play="playVideo"
            @edit="editTag"
            @delete="deleteTag"
            @mouseover="setActive(true)"
          >
            <template #start>
              <BaseCheckbox
                name="check-tag"
                class="text-secondary"
                :model-value="isTagChecked(tag)"
                @input="checkTag(tag)"
              />
            </template>
          </EventTagsListItem>
        </li>
      </ul>
      <!-- Will be implemented in future. -->

      <EventTagsFooter
        :tags-ids="checkedTagsIds"
        :can-show-action-button="Boolean(checkedTagsIds.length)"
        @delete="deleteTags"
      />
    </div>
  </template>
  <template v-else>
    <div class="flex flex-col items-center w-full px-4 py-10 text-center">
      <IconTag fill="var(--base-icon-color)" :width="32" :height="32" />
      <template v-if="!isTaggingModeEnabled">
        <p class="py-6 text-body-medium text-secondary">
          {{ t("pages.events.tabs.event_tags.title_enabled_tagging") }}
        </p>
      </template>
      <template v-else-if="isLoggedIn">
        <p class="py-6 text-body-medium text-secondary">
          {{ t("pages.events.tabs.event_tags.title_no_tags") }}
        </p>
        <BaseButton name="createTag" class="mb-5" type="primary" :disabled="hasPlayerError || isEventStartingSoon" @click="createTag">
          <div class="flex items-center gap-2">
            <IconTag :width="16" :height="16" fill="#ffff" />
            <span class="text-button-bold ">{{ t("pages.events.tabs.event_tags.create_tag") }}</span>
          </div>
        </BaseButton>
      </template>
      <template v-else>
        <p class="py-6 text-body-medium text-secondary">
          {{ t("pages.events.tabs.event_tags.title_no_tags_not_logged_in") }}
        </p>
        <AuthButton
          mode="sign-in"
          :title="t('labels.sign_in')"
          class="font-bold text-white"
          :redirect-url="route.fullPath"
        />
      </template>
    </div>
  </template>
</template>
