<script setup lang="ts">
import Cookies from "js-cookie";
import useEventLock from "~/composables/events/use-event-lock";
import { ERROR_TYPE, EVENT_STATUS, EVENT_TYPE, SEGMENT_TRACK_EVENTS } from "~/constants";
import useEventStore from "~/stores/event";
import formatDate from "~/utils/format-date";

export interface EventTabsProps {
  event: IModelEvent | null;
}
const route = useRoute();
const segment = useSegment();
const { t, locale } = useI18n();
const appConfig = useRuntimeConfig().public;

const props = defineProps<EventTabsProps>();

const eventStore = useEventStore();
const isRealKickoffTimeEnabled = appConfig.APP_CUSTOMER.enable_real_kickoff_time;
const isTagsAvailable = appConfig.APP_CUSTOMER.web_sdk.enable_tagging_mode;
const isEnableMatchStats = appConfig.APP_CUSTOMER.enable_match_stats;

const isEventAvailableForUser = computed(() => eventStore.data?.isEventAvailableForUser);
const isEventAvailableInUserRegion = computed(() => eventStore.data?.isEventAvailableInUserRegion);

const { lockMessage, isLocked, isGeoBlocked, isAuthLock } = useEventLock({
  commerceCoverage: props.event?.commerceCoverage || ["commerce_free_media"] as TCommerceCategory[],
  isEventAvailableForUser,
  isEventAvailableInUserRegion,
});

const activeTabIndex = ref(0);
const videoId = computed(() => route.params.eventId as string || undefined);
const isCreatingClip = computed(() => eventStore.clips.isCreatingClip);
const isCreatingTag = computed(() => eventStore.tags.isCreatingTag);
const isEditingTag = computed(() => eventStore.tags.isEditingTag);
const isEditingClip = computed(() => eventStore.clips.isEditingClip);
const hasMediaView = computed(() => Boolean(route.query.mediaId));
const hasModalOpen = computed(() => isCreatingTag.value || isCreatingClip.value || isEditingTag.value || isEditingClip.value);
const hasTabs = computed(() => isTagsAvailable && !hasMediaView.value && !hasModalOpen.value);
const eventGameStats = computed(() => eventStore.gameStats);
const eventScoreStats = computed(() => eventStore.scoreStats);
const hasGeniusStats = computed(() => Boolean(props.event?.geniusId));
const hasStatisticsTab = computed(() => hasGeniusStats.value || ((eventScoreStats.value || eventGameStats.value) && isEnableMatchStats));
const isExternal = computed(() => eventStore.data?.isExternal);
const eventDate = computed(() => props.event?.gameStartDate);
const isUpcoming = computed(() => props.event?.eventStatus === EVENT_STATUS.UPCOMING);
const formattedEventDate = computed(() => formatDate(eventDate.value, "PPPp", locale.value as TAvailableLocale));
const liveStreamErrorDelay = computed(() => appConfig.APP_CUSTOMER.live_stream_error_delay * 1000);
const countDownText = computed(() => {
  return isRealKickoffTimeEnabled ? t("pages.events.time_till_kick_off") : t("pages.events.event_start_date", { date: formattedEventDate.value });
});

const showCountdown = ref(isRealKickoffTimeEnabled && (Date.now() < Number(eventDate.value)));
let countdownFinishTimer: any = null;

const onLiveStreamErrorTimeout = () => {
  if (countdownFinishTimer)
    return;

  countdownFinishTimer = setTimeout(() => {
    if (!eventStore.data?.source?.hd) {
      eventStore.playerError = ERROR_TYPE.STREAM_ERR;
      Cookies.set(`streamError_${eventStore.data?.id}`, "true", { expires: 1 });
    }
  }, liveStreamErrorDelay.value);
};

const onFinishCountdown = () => {
  if (isRealKickoffTimeEnabled) {
    showCountdown.value = false;
    return;
  }
  eventStore.isEventStartingSoon = true;
  onLiveStreamErrorTimeout();
};

const onTimeCountdownUpdate = () => {
  if (!isRealKickoffTimeEnabled)
    return;

  if (isUpcoming.value && Date.now() > Number(props.event?.date)) {
    eventStore.isEventStartingSoon = true;
    onLiveStreamErrorTimeout();
  }
};

const checkForExistingError = () => {
  const errorState = Cookies.get(`streamError_${eventStore.data?.id}`);

  if (eventStore.data?.source?.hd) {
    Cookies.remove(`streamError_${eventStore.data?.id}`);
    return;
  }

  if (errorState === "true")
    eventStore.playerError = ERROR_TYPE.STREAM_ERR;
};

const onClickFullEvent = () => {
  if (props.event?.eventType !== EVENT_TYPE.USER_CLIP && props.event?.eventType !== EVENT_TYPE.HIGHLIGHT)
    return;

  if (!props.event?.eventId)
    return;

  const segmentPayload = {
    type: props.event?.eventType === EVENT_TYPE.USER_CLIP ? EVENT_TYPE.CLIP : EVENT_TYPE.HIGHLIGHT,
    event_id: props.event.eventId,
    clip_id: props.event?.eventType === EVENT_TYPE.USER_CLIP ? props.event?.id : undefined,
    highlight_id: props.event?.eventType === EVENT_TYPE.HIGHLIGHT ? props.event?.id : undefined,
  };

  return segment.track(SEGMENT_TRACK_EVENTS.BACK_TO_FULL_EVENT_CLICKED, segmentPayload);
};

watch(hasModalOpen, () => {
  if (Date.now() < Number(eventDate.value))
    showCountdown.value = !showCountdown.value;
});

watch(() => eventStore.data?.source?.hd, () => {
  if (eventStore.data?.source?.hd) {
    if (countdownFinishTimer)
      clearTimeout(countdownFinishTimer);

    eventStore.playerError = null;
    Cookies.remove(`streamError_${eventStore.data?.id}`);
  }
});

onMounted(() => {
  checkForExistingError();
});
</script>

<template>
  <div class="relative mt-6 lg:mt-0">
    <div v-if="isUpcoming || showCountdown" class="h-full px-4 py-8 overflow-auto background-main rounded-main text-main xl:px-9">
      <div class="flex items-center justify-center mb-8">
        <IconCalendar fill="var(--base-icon-color)" class="mx-1" :height="16" :width="16" />
        <span class="mx-1 text-body-medium">
          {{ countDownText }}</span>
      </div>
      <EventCountDown :date="eventDate" @timeupdate="onTimeCountdownUpdate" @finish="onFinishCountdown" />
    </div>

    <BaseTabs v-else-if="hasTabs" v-model:active-index="activeTabIndex">
      <BaseTab v-if="hasStatisticsTab" :heading="t('labels.stats')" :icon="{ name: 'stats', path: `/events/stats/${event?.id}` }">
        <div class="background-main rounded-main text-main overflow-auto h-[calc(100%-76px)] w-full lg:aspect-[1]">
          <EventStats :event="event" :score-stats="eventScoreStats" :game-stats="eventGameStats" />
        </div>
      </BaseTab>
      <BaseTab :heading="t('labels.highlights', 0)">
        <div class="background-main rounded-main text-main overflow-auto h-[calc(100%-76px)] w-full lg:aspect-[1]">
          <div v-if="isLocked" class="flex flex-col items-center justify-center px-6 py-12 text-main">
            <IconLock fill="var(--base-icon-color)" width="24" height="32" />

            <p v-if="!isAuthLock && isGeoBlocked" class="my-6 text-center text-heading-3 text-secondary">
              {{ t('labels.lock_event_message.event_restricted') }}
            </p>
            <p v-else class="my-6 text-center text-heading-3 text-secondary">
              {{ lockMessage }}
            </p>
          </div>

          <EventVideos v-else />
        </div>
      </BaseTab>
      <BaseTab :heading="t('labels.tag', 2)">
        <div class="background-main rounded-main text-main overflow-auto h-[calc(100%-76px)] w-full lg:aspect-[1]">
          <div v-if="isLocked" class="flex flex-col items-center justify-center px-6 py-12 text-main">
            <IconLock fill="var(--base-icon-color)" width="24" height="32" />

            <p v-if="!isAuthLock && isGeoBlocked" class="my-6 text-center text-heading-3 text-secondary">
              {{ t('labels.lock_event_message.event_restricted') }}
            </p>
            <p v-else class="my-6 text-center text-heading-3 text-secondary">
              {{ lockMessage }}
            </p>
          </div>
          <div v-else-if="isExternal" class="flex flex-col items-center justify-center px-6 py-12 text-main">
            <IconLock fill="var(--base-icon-color)" width="24" height="32" />
            <p class="py-6 text-center text-body-medium text-secondary">
              {{ t('pages.events.tabs.event_tags.is_external_content') }}
            </p>
          </div>

          <EventTagsList v-else />
        </div>
      </BaseTab>
    </BaseTabs>

    <EventTagsForm v-else-if="isCreatingTag || isEditingTag" :media-item="event" />
    <EventVideosForm v-else-if="isCreatingClip || isEditingClip" :media-item="event" />

    <div v-else-if="hasMediaView" class="h-full">
      <div
        class="px-4 py-3 mb-6"
        style="background: var(--base-card-bgColor); border-radius: var(--base-border-radius);"
      >
        <BaseButton
          class="flex items-center"
          tag="nuxt-link"
          type="icon"
          :name="t('labels.watch_full_game')"
          :to="{ path: `/events/${videoId}/` }"
          @click.capture="onClickFullEvent"
        >
          <IconChevronLeft :width="12" :height="12" class="mr-2 text-main" />
          <div class="text-lg font-medium text-main">
            {{ t("labels.watch_full_game") }}
          </div>
        </BaseButton>
      </div>

      <div class="background-main rounded-main text-main overflow-auto h-[calc(100%-76px)]">
        <EventVideos />
      </div>
    </div>
  </div>
</template>
